
// 下面几个变量，也是外部可以方便控制的变量
@pad-icon-size: 60px;
@pad-icon-padding: 12px;
@border-radius: 50px;
// radius 取 50px 给大的图标一定的空间

@icon-unknown-bg: #F5565A;
@icon-word-bg: #4A89DC;
@icon-ppt-bg: #FFAE00;
@icon-excel-bg: #94C93B;
@icon-pdf-bg: #E9573F;
@icon-audio-bg: #00C594;
@icon-video-bg: #994DDE;
@icon-image-bg: #FF7878;
@icon-new-question-bg: #7CA5F9;
@icon-paper-bg: #FD8A46;
@icon-new-paper-bg: @icon-paper-bg;

.pad-file-icon(@image, @bg-color) {
  background-image: url(@image);
  background-color: @bg-color;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  padding: @pad-icon-padding;
  background-clip: padding-box;
  background-origin: content-box;
}

.pad.icon {
  .pad-file-icon('~assets/img/unknown.png', @icon-unknown-bg);
  width: @pad-icon-size;
  height: @pad-icon-size;
  box-sizing: border-box;
  border: none;
  display: inline-block;
  border-radius: @border-radius;
}

.pad.word.icon, .pad.ft-2020.icon {
  .pad-file-icon('~assets/img/word.png', @icon-word-bg);
}
.pad.ppt.icon, .pad.ft-2024.icon {
  .pad-file-icon('~assets/img/ppt.png', @icon-ppt-bg);
}
.pad.excel.icon, .pad.ft-2028.icon {
  .pad-file-icon('~assets/img/excel.png', @icon-excel-bg);
}
.pad.pdf.icon, .pad.ft-2021.icon {
  .pad-file-icon('~assets/img/pdf.png', @icon-pdf-bg);
}
.pad.audio.icon, .pad.ft-2023.icon {
  .pad-file-icon('~assets/img/audio.png', @icon-audio-bg);
}
.pad.video.icon, .pad.ft-2004.icon {
  .pad-file-icon('~assets/img/video.png', @icon-video-bg);
}
.pad.image.icon, .pad.ft-2022.icon {
  .pad-file-icon('~assets/img/image.png', @icon-image-bg);
}
.pad.new-question.icon, .pad.ft-2025.icon {
  .pad-file-icon('~assets/img/new-question.png', @icon-new-question-bg);
}
.pad.paper.icon, .pad.ft-2018.icon {
  .pad-file-icon('~assets/img/paper.png', @icon-paper-bg);
}
.pad.paper.icon, .pad.ft-2035.icon {
  .pad-file-icon('~assets/img/paper.png', @icon-paper-bg);
}
.pad.paper.icon, .pad.ft-2032.icon {
  .pad-file-icon('~assets/img/zip.png', @icon-paper-bg);
}



