.quesiton-slide-show {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main-section {
  flex: 1;
  height: 50vh;
  padding: 8px 25px;
  background-color: #fff;
  overflow-y: scroll;
  .label, .type-label {
    font-size: .75em;
    font-family: initial;
  }
  .type-label {
    background-color: #333;
    color: white;
    padding: .05em .2em;
    border-radius: .1em;
  }
  &::-webkit-scrollbar {
    width: 0;
    background: transparent;
  }
  .options {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .option {
      flex-grow: 1;
      margin-right: 10px;
    }
    .seq-text {
      color: #000;
      font-weight: bold;
      display: inline-block;
      width: 1em;
      text-align: right;
    }
  }
}

.ui.accordion {
  p {
    margin: 0;
  }
  margin-top: 1rem;
  .title:not(.ui) {
    font-family: initial;
    font-size: 1.2rem !important;
    // font-weight: bold;
    padding: 0;
    .accordion-title-label {
      background-color: #444;
      color: white;
      padding: .2rem 0 .1rem .2rem;
      border-radius: .1rem;
    }
  }
  .tag-list {
    font-family: initial;
    font-size: 1.3rem;
    .item-title-label {
      font-weight: bold;
    }
    .ui.label {
      // font-weight: normal;
      font-size: inherit;
    }
  }
}

.ui.accordion:not(.styled) .accordion .title~.content:not(.ui), .ui.accordion:not(.styled) .title~.content:not(.ui) {
  padding: 0;
}


