@import '~assets/css/base-variables.less';

.richtext {
  word-wrap: break-word;
  .link {
    font-size: inherit;
    text-decoration: underline;
    &:hover, &:active, &:focus {
      text-decoration: underline;
    }
  }
  img {
    max-width: 400px;
  }
  .image {
    display: inline-block;
    vertical-align: middle;
  }

  .bold, .toc {
    font-weight: bold;
  }

  .italic {
    font-style: italic;
  }

  .underscore {
    text-decoration: underline;
  }

  .annotation {
    color: #FF9999;
  }
  .formula {
    margin: .4em;
  }

  .placeholder {
    color: @color-primary;
    display: inline-block;
    padding: 0 1em;
    margin-left: .3em;
    margin-right: .3em;
    border-bottom: 2px solid @color-primary;
    cursor: default;
    font-weight: bold;
    line-height: 1em;
    transition: opacity .1s ease,background-color .1s ease,color .1s ease,box-shadow .1s ease,background .1s ease;

    &.bracket {
      border-bottom: none;
      padding: 0;
      word-spacing: 30px;
      color: inherit;
      &:hover {
        background-color: inherit;
        color: inherit;
      }
    }

    &:hover {
      background-color: @color-primary;
      border-bottom-color: @color-primary;
      color: #fff;
    }
  }
}



